<script>
import IndexForm from '@/components/IndexForm';
export default {
  components: {
    IndexForm,
  },
  data() {
    return {
      etapText: [ //   
        "  ,    ,  (   ).        .",
        "  .     ,    .",
        "       .        <a class=\"portf-call\" href=\"/sozdanie#response\"><b>  </b></a> ..                  ,     ,    .",
