        " .     ,   SEO       .",
        "     .     XSS   SQL .",
        "    .        .      SEO . SEO&mdash;     -."
      ],
    }
  },
  methods: {
    etapChoice(e) {
      let li = e.target;
      if (!li) return;
      let allLi = document.querySelectorAll('#etap ul li');
      for (let i = 0; i < allLi.length; i++) {
        allLi[i].classList.remove('etap_active');
      }
      li.classList.add('etap_active'); //   li
      let n = li.dataset.n;
      document.getElementById('etap_target').innerHTML = this.etapText[n]; //    
    },
    anchors() {
      this.$router.afterEach((to, from) => {
       let h = location.hash;
        let anchor = h.slice(1); //    #
        let el = document.getElementById(anchor);
        let top = el.offsetTop + el.offsetParent.offsetTop;
        window.scrollTo(0, top);
    })
    },
    goTop() {
      window.scrollTo(0, 0);
    },
  },
  mounted() {

  }
}
</script>