<script>
export default {
  methods: {
    anchors() {
      this.$router.afterEach((to, from) => {
       let h = location.hash;
        let anchor = h.slice(1); //    #
        let el = document.getElementById(anchor);
        let top = el.offsetTop + el.offsetParent.offsetTop;
        window.scrollTo(0, top);
    })
    },
  },
  mounted(){
    let h = location.hash;
    if(h){
      let anchor = h.slice(1); //    #
      let el = document.getElementById(anchor);
      let top = el.offsetTop + el.offsetParent.offsetTop;
      window.scrollTo(0, top);
    }
  }
}
</script>