        </li>
        <li class="mob-link">
          <router-link @click="anchors" :to="{name: 'context'}"  title=" "> 
          </router-link>
        </li>
      </ul><!--noindex--><strong class="mob-menu-header" title=" "> </strong>
      <!--/noindex-->
      <ul>
        <li class="mob-link"><a  title=" " href="/parsing"> </a>
        </li>
      </ul>
    </nav>
    <!--/noindex-->
    <!--noindex-->
    <div class="mob-menu-col">
      <div @click.stop="showMenu" ref="menuBtn" class="mob-menu-button" title=""></div>
    </div>
    <!--/noindex-->
    <!--  ""   -->
    <!--  ""   -->
    <nav id="l_menu">
      <router-link :to="{name: 'sozdanie'}" class="l_header link-anim" title=" "> 
      </router-link>
      <ul>
        <li class="left_gr2">
          <router-link @click="anchors" :to="{name: 'sait_vizitka'}" title=" "> 
          </router-link>
        </li>
        <li class="left_gr2">
          <router-link @click="anchors" :to="{name: 'korporativniy_sait'}" title=" "> 
          </router-link>
        </li>
        <li class="left_gr2">
          <router-link @click="anchors" :to="{name: 'internet_magazin'}" title=" "> 
