          </router-link>
        </li>
      </ul>
      <br/><!--noindex--><strong class="l_header"> </strong><!--/noindex-->
      <br/>
      <ul>
        <li class="left_gr2">
          <router-link @click="goTop" :to="{name: 'parsing'}" title=" "> 
          </router-link>
        </li>
      </ul>
    </nav>
    <!--  ""   -->
  </div>
</template>
<script>
export default {
  emits: ['mobMenuEmit'],
  data() {
    return {
      showMobMenu: false,
    }
  },
  methods: {
    anchors() {
      this.$router.afterEach((to, from) => {
       let h = location.hash;
        let anchor = h.slice(1); //    #
        let el = document.getElementById(anchor);
        let top = el.offsetTop + el.offsetParent.offsetTop;
        window.scrollTo(0, top);
    })
    },
    showMenu() {
      // console.log(this.$refs.mobMenu);
      const mobMenu = this.$refs.mobMenu;
      const menuBtn = this.$refs.menuBtn;
      this.showMobMenu = true;
      mobMenu.classList.toggle('menu-animate');
      menuBtn.classList.toggle('btn-pos');
      this.$emit('mobMenuEmit');
    },
    goTop() {
      window.scrollTo(0, 0);
    },
  }
}
</script>